.c-header__opener {
   background-color: transparent;
   border: none;
   transition: all 0.3s;
   display: flex;
   position: relative;
   cursor: pointer;
   padding: 0;
   z-index: 999;
   margin-left: 48px;
   background-color: var(--color-blue-light);
   width: 34px;
   height: 34px;
   align-items: center;
   justify-content: center;
   border-radius: 50%;
   @include media-breakpoint-down(md) {
     margin-left: auto;
   }
   span {
     transition: all 0.3s;
     width: 13px;
     height: 1px;
     background: #fff;
     transform: translateY(-0px);
     display: block;
     &::before,
     &::after {
       transition: all 0.3s;
       content: "";
       position: absolute;
       width: 13px;
       height: 1px;
       background-color: var(--color-light);
       left: 0;
       right: 0;
     }
     &::before {
       transform: translateY(-4px);
     }
     &::after {
       transform: translateY(4px);
     }
   }
   &:focus {
     box-shadow: none;
   }
 }
 .c-header__opener.is-open {
   span {
     border-color: var(--color-blue-light);
     background: var(--color-blue-light);
   }
   span:before {
     transform: rotate(45deg);
     background-color: var(--color-light);
   }
   span:after {
     transform: rotate(-45deg);
     background-color: var(--color-light);
   }
 }